$time: .3s;

.cripto-wrapper {
    width: 100%;
    max-width: 1100px;
    margin: auto;
    border: 2px solid #333;
    height: 400px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    position: relative;
    overflow: hidden;
}

.select {
    padding: 5px 10px;
    border-radius: 3px;
    cursor: pointer;
    font-size: 20px;
    outline-color: #666;
}

#criptoInfo {
    height: 20px;
    font-size: 40px;
    margin-top: 20px;
}
.button{
    margin-top: 40px;
    cursor: pointer;
    background-color: #666;
    color: #fff;
    border: none;
    padding: 5px 10px;
    font-size: 30px;
    align-self: flex-end;
    position: absolute;
    right: -150px;
    top: 50px;
    transition: $time;
    border-radius: 3px;
    
    &--calc{
        top: 220px;
        left: 50%;
        transform: translateX(-50%);
        
    }
    &:hover{
        right: 0;
    }
}
.calculate_wrapper{
    flex-direction: row;
}
.select-wrap{
    text-align: center;
}
.calculate-symbol{
    font-size: 150px;
}
#result{
    position: absolute;
    top: 90px;
    font-size: 30px;
    font-weight: bold;
    text-align: center;
    font-family: 'Arial', sans-serif;
}
#result .media-item{
    text-transform: uppercase;
    color: #fff;
    text-shadow: 2px 2px 23px #666;
    user-select: none;
}